<template>
	<transition name="move">
	<div v-show="showFlag" class="food" ref="food">
		<div class="food-content">
			<div class="image-header">
				<img :src="food.image">
				<div class="back" @click="hide">
				<i class="icon-arrow_lift"></i>
			</div>
		</div>
			<div class="content">
				<h1 class="title">{{food.name}}</h1>
				<div class="detail">
					<span class="sell-count">
						月售{{food.sellCount}}
					</span>
					<span class="rating">
						好评率{{food.rating}}%
					</span>
				</div>
				<div class="price">
					<span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
				</div>
				      <div class="cartcontrol-wrapper">
            	<cartcontrol :food=food @add="addFood"></cartcontrol>
            </div>
            <transition name="fade">
            <div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count===0">
            	加入购物车
            </div>
            </transition>
	        </div>
	        <split></split>
	             <div class="info">
	        	<h1 class="title">商品信息</h1>
	        	<p class="text" v-show="food.info">{{food.info}}</p>
	        </div>
            <split></split>
            <div class="rating">
            	<h1 class="title">商品评价</h1>
            	<ratingselect :selectType="selectType" :onlyContent='onlyContent' :desc='desc' :ratings="food.ratings" @select="selectRating" @toggle="toggleContent"></ratingselect>
            </div>
            <div class="rating-wrapper">
            	<ul v-show="food.ratings && food.ratings.length">
            		<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
            			<div class="user">
            				
            					<span class="name">{{rating.username}}</span>
            					<img :src="rating.avatar" class="avatar" width="12" height="12">
            			</div>
            				<div class="time">{{rating.rateTime | formaDate}}</div>
            				<p class="text">
            					<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
            				</p>
            			
            		</li>
            	</ul>
            	<div class="no-rating" v-show="!food.ratings||!food.ratings.length">暂无评价</div>
            </div>

			</div>
		</div>
		
	</div>

	</transition>

</template>

<script>
	import BScroll from 'better-scroll';
	import cartcontrol from '../cartcontrol/cartcontrol'
	import {formatDate} from '../../common/js/date'
	import split from '../split/split'
	import ratingselect from '../ratingselect/ratingselect'
	import Vue from 'vue';
	const POSITIVE = 0;
	const NEGATIVE = 1;
	const ALL = 2;
	export default{
		props: {
			food:{
				type:Object
			}
		},
		data() {
			return {
				showFlag:false,
				selectType: ALL,
				onlyContent: true,
				desc: {
					all: '全部',
					positive: '推介',
					negative: '吐槽'
				}
			}
		},
		methods: {
			show() {
				this.showFlag=true
				this.selectType = ALL
				this.onlyContent = false
				this.$nextTick(()=> {
					if(!this.scroll){
					this.scroll = new BScroll(this.$refs.food, {
                    		click:true
                    	});
				}else{
						this.scroll.refresh();
					}

				});
			},
			hide() {
				this.showFlag=false
			},
			addFirst(event) {
               if(!event._constructed){
                  return;
               }
               this.$emit('add', event.target);
               Vue.set(this.food,"count",1);
			},
			  addFood(target) {
               this.$emit('add', target);
      },
              selectRating(type){
              	this.selectType=type;
              	this.$nextTick(()=> {
              		this.scroll.refresh();
              	})
              },
              toggleContent(){
              	this.onlyContent=!this.onlyContent;
              		this.$nextTick(()=> {
              		this.scroll.refresh();
              	})
              },
              needShow(type,text) {
              	if(this.onlyContent && !text){
              		return false;
              	}
              	if (this.selectType === ALL) {
              		return true;
              	} else {
                    return type === this.selectType; 
              	}

              }
		},
		filters: {
         formaDate(time) {
         	let date=new Date(time);
         	return formatDate(date,'yyyy-MM-dd hh:mm');
         }
		},
		components: {
			cartcontrol,
			split,
			ratingselect
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin.styl"
.food
	position: fixed
	left: 0
	top: 0
	bottom: 48px
	z-index: 30
	width: 100%
	background: #fff
	transform: translate3d(0,0,0)
	&.move-enter-active, &.move-leave-active
		transition: all 0.2s linear
	&.move-enter, &.move-leave-active
		transform: translate3d(100%,0,0)
	.image-header
		position: relative
		width: 100%
		height: 0
		padding-top: 100%
		img
			position: absolute
			width: 100%
			height: 100%
			top: 0
			left: 0
		.back
			position: absolute
			top: 10px
			left: 0px
			.icon-arrow_lift
				display: block
				padding: 10px
				font-size: 20px
				color: #fff
	.content
		padding: 18px
		position: relative
		.title
			font-size: 14px
			font-weight: 700
			color: rgb(7,17,27)
			margin-bottom: 8px
			line-height: 14px
		.detail
			height: 10px
			margin-bottom: 18px
			font-size: 0
			line-height: 10px
			.sell-count, .rating
				color: rgb(147,153,159)
				font-size: 10px
			.sell-count
				margin-right: 12px
		.price
			font-weight: 700
			line-height: 24px
			.now
				font-size 14px
				margin-right: 8px
				color rgb(240,20,20)
			.old
				font-size: 10px
				color: rgb(147,153,159)
				text-decoration:line-through
		.cartcontrol-wrapper
			position: absolute
			right: 12px
			bottom: 12px
		.buy
			position: absolute
			right: 18px
			bottom: 18px
			z-index: 10
			height: 24px
			line-height: 24px
			padding: 0 12px
			box-sizing: border-box
			font-size: 10px
			border-radius: 12px
			color: #fff
			background: rgb(0,160,220)
			opacity: 1
			&.fade-enter-active, &.fade-leave-active
				transition: all 0.2s
			&.fade-enter, &.fade-leave-active
				opacity: 0
				z-index: -1
	.info
		padding: 18px
		.title
			font-size: 14px
			line-height: 14px
			margin-bottom: 6px
			color: rgb(7,17,27)
		.text
			font-size: 12px
			color: rgb(77,85,93)
			line-height: 24px
	.rating
		padding-top: 18px
		.title
			font-size: 14px
			line-height: 14px
			margin-left: 18px
			color: rgb(7,17,27)
	.rating-wrapper
		padding: 0 18px
		.rating-item
			position: relative
			padding: 16px 0
			border-1px(rgba(7,17,27,0.1))
			.user
				position: absolute
				right: 0
				top: 16px
				line-height: 12px
				font-size:0
				.name
					display: inline-block
					vertical-align: top
					margin-right: 6px
					font-size: 10px
					color: rgb(147,153,159)
				.avatar
					border-radius: 50%
			.time
				line-height: 12px
				font-size: 10px
				color: rgb(147,153,159)
				margin-bottom: 6px
			.text
				font-size: 12px
				line-height: 16px
				color: rgb(7,17,27)
				.icon-thumb_up, .icon-thumb_down
					margin-right: 4px
					color: rgb(0,160,220)
					line-height: 16px
					font-size: 12px
				.icon-thumb_down
					color: rgb(147,153,159)
	.no-rating
		padding: 16px 0
		font-size: 12px
		color: rgb(147,153,159)
					
					
				
					
					
		
			
		
			
			

		
			
			
				
			
			
		
		
		
</style>


