import Vue from 'vue';
import App from './App';
import VueRouter from 'vue-router';
import VueResouce from 'vue-resource';
import router from './router/index.js';

import './common/stylus/index.styl';

Vue.use(VueResouce);
/* eslint-disable no-new */


/* eslint-disable no-new */
new Vue({
  el: '#app',
    router,
  render: h => h(App)
});



